.iconClose {
    position: fixed;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    z-index: 0;
}

.width {
    width: 100%;
}

.header {
    position: sticky;
    top: 0;
    z-index: 100;

    &__nav {
        background: #17232b;
        height: 85px;
        display: flex;
        position: relative;
        box-shadow: 0 0 15px var(--color-2);
        z-index: 104;
    }

    &__nav .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }

    &__logo {
        font-family: "Jost";
        font-weight: 700;
        font-size: 30px;
        line-height: 32px;
        color: #fff;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 35px;
        transition: 400ms;
        @include media(992){
            position: fixed;
            flex-direction: column;
            top: 90px;
            left: -200px;
            background: #17232b;
            padding: 20px;
            width: 200px;
            height: 70%;
            border-radius: 0 0 30px 0;
        }
    }

    &__link {
        font-family: "Jost";
        font-weight: 700;
        font-size: 20px;
        line-height: 32px;
        color: #fff;
        transition: 300ms;

        &:hover {
            color: var(--color-2);
        }

        &.active {
            color: var(--color-1);
        }
    }

    &__btn {
        cursor: pointer;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        color: var(--color-1);
        z-index: 102;
        transition: 500ms;

        &.active {
            box-shadow: 0px 0px 25px var(--color-2);
        }

        &.active .header__icon {
            color: var(--color-1);
        }

        &.active+.header__dropdown {
            height: 50px;
            top: 90px;
            box-shadow: 0px 0px 15px var(--color-2);
        }
    }

    &__icon {
        font-size: 30px;
        line-height: 32px;
        color: #fff;
        transition: 300ms;

        &:hover {
            color: var(--color-2);
        }

        &.active {
            color: var(--color-1);
            text-shadow: 0px 0px 25px var(--color-1);
        }
    }

    &__dropdown {
        overflow: hidden;
        background: var(--nav);
        width: 125px;
        border-radius: 30px;
        position: absolute;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0px;
        right: 0;
        top: 116px;
        transition: 300ms;

        @include media(576) {
            right: 15px;
        }

        & button {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 50px;
            width: 50px;
            border-radius: 50%;
            transition: 500ms;
            z-index: 3;
            font-size: 20px;
            font-weight: 700;
            color: var(--color-2);
        }

        & button.active {
            color: white;
        }

        &-span {
            position: absolute;
            border-radius: 50%;
            height: 50px;
            width: 50px;
            transition: 500ms;
            background: var(--color-1);
            z-index: 2;
            transform: translateX(75px);

            &.active {
                transform: translateX(0);
            }
        }
    }
}