.intro__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 50px;
}

.intro__text {
    font-family: 'Jost';
    font-weight: 600;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: .4px;
    color: #fff;
    margin-bottom: 20px;
}

.intro__title {
    font-family: 'Inter';
    font-weight: 800;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 2.2px;
    color: #fff;
    margin-bottom: 20px;
    text-align: center;
    
}

.intro__subtitle {
    font-family: 'Jost';
    font-weight: 600;
    font-size: 17px;
    line-height: 32px;
    letter-spacing: .6px;
    color: var(--color-1);
    margin-bottom: 30px;
    @include media(443){
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.intro__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 30px;
    row-gap: 30px;
    @include media(443){
        flex-direction: column;
    }
}

.intro__img {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 70px;
    max-width: 275px;
    width: 100%;
    height: 275px;
    border-radius: 70px 70px 20px 20px;
    background: linear-gradient(179.92deg, var(--color-1) .07%, rgba(248, 242, 244, .5) 134.98%);
    transition: all .4s linear;
    @include media(400){
        max-width: 230px;
        height: 230px;
    }
    & img {
        width: 100%;
    }
}

.intro__img:hover {
    transform: scale(1.07);
    box-shadow: 0 0 25px var(--color-2);
}