.contacts {
  padding-bottom: 130px;

  @include media(992) {
    padding-bottom: 100px;
  }

  @include media(768) {
    padding-bottom: 75px;
  }

  @include media(576) {
    padding-bottom: 60px;
  }
}

.contacts__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 100px;

  @include media(992) {
    margin-top: 100px;
  }

  @include media(768) {
    margin-top: 75px;
  }

  @include media(576) {
    margin-top: 60px;
  }
}

.contacts__items {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 50px;
}

.contacts__item {
  width: 100px;
  height: 100px;
  background: #182228;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .4s linear
}

.contacts__icon {
  font-size: 45px;
  color: var(--color-1);
  transition: .4s linear;
}

.contacts__item:hover {
  transform: rotate(30deg);
  box-shadow: 0 0 25px var(--color-2);
}

.contacts__item:hover .contacts__icon {
  color: #fff;
  transform: rotate(-60deg);
}

.contacts__img {
  display: block;
  max-width: 275px;
  width: 100%;
  height: 275px;
  background: linear-gradient(179.92deg, var(--color-1) .07%, rgba(248, 242, 244, .5) 134.98%);
  border-radius: 30px;
  transition: all .4s linear;
  margin: 75px auto;

  @include media(992) {
    max-width: 250px;
    height: 250px;
  }

  @include media(768) {
    max-width: 200px;
    height: 200px;
  }

  & img {
    width: 100%;
  }
}

.contacts__img:hover {
  transform: scale(1.1) rotate(10deg);
  box-shadow: 0 0 25px var(--color-2);
}