$Inter: ('Medium', 500),
        ('SemiBold', 600),
        ('Bold', 700),
        ('ExtraBold', 800);

@each $name, $weight in $Inter {
  @font-face {
    font-family: 'Inter';
    src: url('@/assets/fonts/Inter/Inter-#{$name}.woff');
    font-weight: $weight;
  }
}

$Jost: ('SemiBold', 600),
        ('Bold', 700);
        

@each $name, $weight in $Jost {
  @font-face {
    font-family: 'Jost';
    src: url('@/assets/fonts/Jost/Jost-#{$name}.woff');
    font-weight: $weight;
  }
}
