.offer__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column
}

.offer__item {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  max-width: 500px;
  width: 100%;
  min-height: 300px;
  margin-top: 100px;
  background: #182228;
  border-radius: 20px;
  padding: 35px 25px;
  gap: 30px;
  transition: 400ms linear;

  &:hover {
    box-shadow: 0 0 15px var(--color-2);

  }

  &.active {
    box-shadow: 0 0 15px var(--color-2);

  }

  .offer__btn {
    position: absolute;
    left: 25px;
    bottom: 35px;
    @include media(576) {
      font-size: 18px;
      line-height: 25px;
      left: 50%;
      transform: translateX(-50%);
    }
    @include media(500) {
      max-width: 216px;
      width: 100%;
    }
  }

  &.active .offer__btn {
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0 0 15px var(--color-2);
    transition: all .4s linear;

    @include media(576) {
      left: 25px;
      transform: translateX(0);
    }

  }

  @include media(992) {
    margin-top: 80px;
  }

  @include media(768) {
    margin-top: 50px;
  }

  @include media(576) {
    align-items: center;
    text-align: center;
  }
}

.offer__item:hover .offer__btn {
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 15px var(--color-2);
  transition: all .4s linear;

}

.offer__item-link {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  row-gap: 20px;

  @include media(576) {
    text-align: center;
  }
}

.offer__item-list {
  display: flex;
  align-items: center;
  justify-content: center;
  row-gap: 20px;
  column-gap: 20px;

  @include media(576) {
    column-gap: 10px;
  }
}

.offer__item-icon {
  font-size: 30px;
  color: var(--color-1);

  @include media(576) {
    font-size: 25px;
  }
}

.offer__item-h3 {
  font-family: 'Jost';
  font-weight: 400;
  color: #fff;
  font-size: 18px;

  @include media(576) {
    font-size: 15px;
  }
}