.about__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 100px;
    @include media(992){
        margin-bottom: 100px;
    }
    @include media(768){
        margin-bottom: 75px;
    }
    @include media(576){
        margin-bottom: 60px;
    }
}

.about__bottom {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    margin-top: 80px;
    @include media(992){
        flex-direction: column;
        row-gap: 40px;
        margin-top: 50px;
    }
}

.about__left {
    max-width: 275px;
    width: 100%;
    height: 275px;
    background: linear-gradient(179.92deg, var(--color-1) .07%, rgba(248, 242, 244, .5) 134.98%);
    border-radius: 30px;
    transition: all .4s linear;
    margin-bottom: 30px;
    @include media(992){
        max-width: 250px;
        height: 250px;
    }
    @include media(768){
        max-width: 200px;
        height: 200px;
    }
    & img {
        width: 100%;
    }
}

.about__left:hover {
    transform: scale(1.1) rotate(10deg);
    box-shadow: 0 0 25px var(--color-2);
}

.about__right {
    max-width: 440px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    @include media(992){
        text-align: center;
        align-items: center;
    }
}

.about__subtitle {
    font-family: 'Inter';
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;
    letter-spacing: .2px;
    color: #959595
}

.about__item {
    width: 260px;
    height: 100px;
    background: #182228;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 65px;
    margin-bottom: 65px
}

.about__icon {
    font-size: 25px;
    color: var(--color-1);
    margin-bottom: 9px
}

.about__text {
    font-family: 'Inter';
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: .6px;
    color: #fff;
    margin-bottom: 6px
}

.about__text-2 {
    font-family: 'Inter';
    font-weight: 600;
    font-size: 10px;
    line-height: 20px;
    letter-spacing: .6px;
    color: #959595
}
