.portfolio__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 100px;
  

  @include media(992) {
    margin-top: 80px;
  }

  @include media(768) {
    margin-top: 50px;
  }

  @include media(576) {
    align-items: center;
    text-align: center;
  }
}

.portfolio__tab {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 70px 0;

  @include media(992) {
    margin: 60px 0;
  }

  @include media(768) {
    margin: 50px 0;
  }

  @include media(576) {
    margin: 40px 0;
  }
}

.portfolio__tab-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px
}

.portfolio__sectoion {
  position: relative;
  width: 100%;
  padding: 100px 0 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  @include media(992) {
    padding: 75px 0 30px;
  }
  @include media(600) {
    padding: 50px 0 20px;
  }

}

.portfolio__web {
  display: grid;
  grid-template-columns: repeat(2, 350px);
  grid-template-rows: repeat(3, 350px);
  gap: 50px;
  transition: .5s linear;

  @include media(992) {
    grid-template-columns: repeat(2, 300px);
    grid-template-rows: repeat(3, 300px);
  }

  @include media(768) {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, 350px);
  }
}

.portfolio__item {
  width: 100%;
  height: 100%;
  background: #182228;
  border-radius: 20px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  row-gap: 30px;
  column-gap: 30px;
  padding: 20px;
  transition: all .2s linear;
  position: relative;

  &.active {
    box-shadow: 0 0 15px var(--color-2);
  }
  
  .portfolio__item-btn {
    position: absolute;
    left: 20px;
    bottom: 35px;
    @include media(992) {
     max-width: 160px;
     width: 100%;
    }
    @include media(576) {
      left: 50%;
      transform: translateX(-50%);
    }
    
  }

  &.active .portfolio__item-btn {
    left: 50%;
    transform: translateX(-50%);
    transition: all .4s linear;
    box-shadow: 0 0 15px var(--color-2);
  }

  &:hover {
    box-shadow: 0 0 15px var(--color-2);
  }

  @include media(992) {
    row-gap: 20px;
    column-gap: 20px;
  }

  @include media(768) {
    row-gap: 30px;
    column-gap: 30px;
  }

  @include media(576) {
    max-width: 250px;
    width: 100%;
    align-items: center;
    padding: 20px;
    height: 300px;
  }
}

.portfolio__item:hover .portfolio__item-btn {
  left: 50%;
  transform: translateX(-50%);
  transition: all .4s linear;
  box-shadow: 0 0 15px var(--color-2);
}

.left:hover {
  transform: scale(1.05) translateX(10px);

  @include media(768) {
    transform: scale(1.05) translateX(0);
  }
}

.right:hover {
  transform: scale(1.05) translateX(-10px);

  @include media(768) {
    transform: scale(1.03) translateX(0);
  }
}

.portfolio__item-h3 {
  font-family: 'Jost';
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: var(--color-1);

  @include media(768) {
    transform: scale(1.05) translateX(0);
  }
}

.portfolio__item-img {
  width: 300px;
  height: 150px;
  object-fit: cover;
  object-position: center;

  @include media(992) {
    width: 250px;
    height: 130px;
  }

  @include media(768) {
    width: 300px;
    height: 150px;
  }
  @include media(768) {
    width: 230px;
    height: 150px;
  }
  @include media(576) {
    width: 230px;
    height: 110px;
  }
}