html {
  scroll-behavior: smooth;
}

:root {
  --color-1: rgb(169, 161, 245);
  --color-2: rgb(245, 221, 161);
  --body: #111a20;
  --nav: #17232b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  border: none;
  outline: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  font-family: 'Inter';
  background: var(--body);
}

@mixin media($size) {
  @media (max-width: #{$size}px) {
    @content;
  }
}

.container {
  max-width: 1140px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;

  @include media(1200) {
    max-width: 960px;
  }

  @include media(992) {
    max-width: 720px;
  }

  @include media(768) {
    max-width: 540px;
  }

  @include media(576) {
    max-width: 100%;
  }
}

.menu {
  cursor: pointer;
  height: 50px;
  width: 50px;
  display: none;
  flex-direction: column;
  justify-content: center;
  row-gap: 5px;
  overflow: hidden;
  background: transparent;
  border: none;
  position: relative;
  transition: 500ms;
  border-radius: 50%;

  &.active {
    box-shadow: 0px 0px 25px var(--color-2);
  }

&.active ~ .iconClose {
    width: 100%;
  }
  &.active ~ .header__list {
    left: 0;
    box-shadow: 0 0 15px var(--color-2);
  }

  @include media(992) {
    display: flex;
  }
}

.menu span {
  display: block;
  width: 35px;
  height: 4px;
  border-radius: 3px;
  background: white;
  transition: all 0.4s linear;
}

.menu:hover span {
  background: var(--color-2);
}

.menu.active span {
  background: var(--color-1);
}

.menu.active span:nth-child(1) {
  transform: rotate(45deg) translateY(-18px) translateY(25px) translateX(29px) translateX(-22px);
}

.menu.active span:nth-child(2) {
  transform: rotate(-45deg);
  background: transparent;
}

.menu.active span:nth-child(3) {
  transform: rotate(-45deg) translateY(19px) translateY(-25px) translateX(32px) translateX(-26px);
}

.button {
  padding: 0 25px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--color-1);
  border-radius: 5px;
  font-family: 'Jost';
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  color: #fff;
  transition: 300ms linear;
  
  &:hover {
    background: var(--color-1);
    color: var(--color-2);
    box-shadow: 0 0 15px var(--color-2);
  }
}


.title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  max-width: 350px;
  width: 100%;
  min-height: 65px;
  & h3 {
      font-family: 'Inter';
      font-weight: 800;
      font-size: 30px;
      line-height: 32px;
      color: var(--color-1);
      filter: drop-shadow(0 0 36px var(--color-2));
  }
  & p {
    font-family: 'Inter';
    font-weight: 800;
    font-size: 15px;
    line-height: 32px;
    color: #767676;
    filter: drop-shadow(0 0 36px rgba(255, 169, 122, .8));
}
}

::-webkit-scrollbar {
  background: var(--nav);
  width: 7px;
  height: 7px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: var(--color-1);
}