.experience {

    &__content {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-bottom: 100px;
        
        @include media(768){
            margin-bottom: 75px;
        }
        @include media(576){
            margin-bottom: 40px;
        }
    }

    &__progressbar {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        width: 100%;
        padding: 100px 50px 50px;
        column-gap: 50px;
        row-gap: 50px;
        @include media(768){
            padding: 50px 0;
        }
    }

    &__card {
        max-width: 220px;
        width: 100%;
        height: 250px;
        border-radius: 20px;
        background: #182228;
        display: flex;
        align-items: center;
        justify-content: center;
        &.active {
            box-shadow: 0 0 15px var(--color-2);
        }
    }

    &__progress {
        position: relative;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        background: #444 linear-gradient(to right, transparent 50%, #444 0);
        transition: 500ms linear;

        &-percent {
            position: absolute;
            top: 35%;
            left: 50%;
            transform: translate(-50%, -45%);
            font-family: 'Inter';
            font-weight: 700;
            font-size: 25px;
            line-height: 20px;
            letter-spacing: .6px;
            color: #fff;
            z-index: 1;
            opacity: 0;
            transition: 500ms linear;
        }

        &-percent span {
            font-family: 'Inter';
            font-size: 13px;
            text-align: center;
            font-weight: 400;
            opacity: 0;
            transition: 500ms linear;
        }

        & h4 {
            position: absolute;
            top: 53%;
            left: 50%;
            transform: translateX(-50%);
            font-family: 'Inter';
            font-weight: 700;
            font-size: 22px;
            line-height: 20px;
            letter-spacing: .6px;
            color: var(--color-1);
            z-index: 1;
            text-transform: capitalize;
            opacity: 0;
            transition: 500ms linear;
        }

        &::before {
            content: '';
            display: block;
            height: 100%;
            margin-left: 50%;
            transform-origin: left;
            border-radius: 0 100% 100% 0/50%;
        }

        &::after {
            content: '';
            position: absolute;
            inset: 3px;
            border-radius: 50%;
            background: #182228;
        }

        &::before {
            background: #444;
            transform: rotate(calc((var(--i) - 50) * 0.01turn));
        }

        &.active {
            background: #444 linear-gradient(to right, transparent 50%, var(--color-1) 0);
        }

        &.active .experience__dot::before {
            box-shadow: 0 0 10px var(--color-2), 0 0 30px var(--color-2);
        }

        &.active h4,
        &-percent,
        &-percent span {
            opacity: 1;
        }

        &.active::before {
            background: var(--color-1);
            transform: rotate(calc((var(--i) - 50) * 0.01turn));
        }

        &.active .experience__card {
            box-shadow: 0 0 15px var(--color-2);
        }
    }

    &__dot {
        position: absolute;
        display: block;
        inset: 5px;
        z-index: 10;
        transform: rotate(calc(3.6deg * var(--num)));
        animation: animDot 1.5s linear forwards;

        &::before {
            content: "";
            position: absolute;
            top: -12px;
            left: 50%;
            border-radius: 50%;
            transform: translateX(-50%);
            width: 15px;
            height: 15px;
            background: var(--color-1);
            box-shadow: 0 0 10px var(--color-1), 0 0 30px var(--color-1);
        }
    }

    &__progress-enter-active,
    &__progress-leave-active {
        opacity: 0;
        transition: 1.5s linear;
    }

    &__progress-enter-to,
    &__progress-leave-from {
        opacity: 1;
        transform: scale(1.2);
        box-shadow: 0 0 50px var(--color-2);
    }
}

@keyframes animDot {
    0% {
        transform: rotate(0)
    }

    100% {
        transform: rotate(calc(3.6deg * var(--i)))
    }
}