.footer {
  min-height: 300px;
  padding: 50px 0 20px;
  background: var(--color-1);
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.footer__title {
  font-family: 'Inter';
  font-weight: 800;
  font-size: 30px;
  line-height: 40px;
  color: #293241;
  margin-bottom: 40px;
  text-align: center;
}

.footer__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 40px;
  row-gap: 40px;
  margin-top: 25px
}

.footer__link {
  font-family: 'Jost';
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  color: #293241;
  transition: 300ms linear;
}

.footer__link:hover {
  color: var(--color-2);
}

.footer__items {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 40px;
  row-gap: 40px;
  margin-bottom: 70px;
}   

.footer__item {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #293241;
  border-radius: 5px;
  transition: .2s linear
}

.footer__icon {
  font-size: 20px;
  color: var(--color-2);
  transition: .3s linear
}

.footer__item:hover {
  transform: rotate(30deg);
  box-shadow: 0 0 15px #fff;
}

.footer__item:hover .footer__icon {
  color: #fff;
  transform: rotate(-60deg)
}

.footer__creator {
  font-family: 'Inter';
  font-weight: 800;
  font-size: 12px;
  line-height: 32px;
  color: #293241
}